<template>
  <div>
    <!-- 输入框 -->
    <input type="text" v-model="endPointInput" placeholder="请输入终点经纬度" />
    
    <!-- 导航按钮 -->
    <button @click="navigate">导航</button>
    
    <!-- 地图组件 -->
    <a-map-walking
      :startPoint="startPoint"
      :endPoint="endPoint"
      style="width: 100%; height: 1000rpx"
    ></a-map-walking>
  </div>
</template>

<script>
import AMapWalking from "../../components/a-map-walking.vue";

export default {
   components: { AMapWalking },
  data() {
    return {
      startPoint: [], // 起点经纬度
      endPointInput: '', // 输入的终点经纬度
      endPoint: [], // 终点经纬度
    };
  },
  created() {
    this.getCurrentPosition();
  },
  methods: {
    // 获取当前位置
    getCurrentPosition() {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { longitude, latitude } = position.coords;
          this.startPoint = [longitude, latitude];
        },
        (err) => {
          console.error(err);
        },
        { enableHighAccuracy: true, maximumAge: 10000, timeout: 27000 }
      );
    },
    // 导航函数
    navigate() {
      if (this.endPointInput) {
        // 解析输入的终点经纬度
        const [lon, lat] = this.endPointInput.split(',').map(parseFloat);
        if (!isNaN(lon) && !isNaN(lat)) {
          // 设置终点经纬度
          this.endPoint = [lon, lat];
        } else {
          alert('请输入有效的经纬度格式');
        }
      } else {
        alert('请输入终点经纬度');
      }
    },
	planRoute() {
	  if (this.startPoint.length && this.endPoint.length) {
	    // 此处可以执行导航操作，比如路径规划等
	    console.log('开始导航，起点:', this.startPoint, '终点:', this.endPoint);
	  } else {
	    alert('请先设置起点和终点');
	  }
	},
	// 设置起点和终点
	setPoint(startP, endP) {
	  this.startPoint = startP;
	  this.endPoint = endP;
	},
  },
};
</script>
